.page article p,
.page article ol,
.page article ul,
.page article figure,
.post article p,
.post article ul,
.post article ol,
.post article figure {
  margin: 0 0 1.75rem;
}

.note {
  margin-top: 4rem;
}

.entry-header,
.page-header {
  margin: 4rem 0;
  text-align: center;
}

.note-header {
  line-height: .4rem;
  border-bottom: .063em solid $border-color;
}

blockquote {
  position: relative;
  margin: 1em 0;
  padding: 0 2em;
  border-left: .05em solid $primary-color;

  &:before {
    position: absolute;
    top: -.15rem;
    left: .6rem;
    font-family: $font-header;
    font-size: 1.7rem;
    content: '\201C';
  }

  .note & {
    border: 0;
  }
}

.cite {
  text-align: right;
}

.call-out {
  position: relative;
  margin: 1em 0;
  padding: 0 1em;
  border-left: .05em solid $primary-color;
}

hr {
  width: 5em;
  height: .05em;
  margin: 2em auto;
  background-color: $border-color;
  border: 0;
}

.content footer {
  text-align: center;
}

.entry-meta {
  font-size: .8rem;

  article footer & {
    margin: 0;
    line-height: 1.65;
  }

  .note & {
    font-size: .65em;
  }

}

.note-meta {
  padding-right: 1em;
  font-size: .65em;
  background-color: #fff;
}

.after-post-title {
  font-family: $font-header;
  font-size: 1.5em;
  font-weight: bold;
}
